.nav {
    background: black;
    color: green;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: auto;
    padding: 0 20px;
    white-space: nowrap;
  }
  
  nav h1 {
    margin: 0;
  }
  
  .after_login {
    color: green;
    background-color: green;
  }


  nav a {
    color: red;
    display: inline-block;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 1.2em;
    text-decoration: none;
  }
  
  /* nav a:hover {
    background: #282C34;
 } */


  nav img {
    height: 50px;
    width: 50px;
    -webkit-animation-name: float;
            animation-name: float;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }

  @-webkit-keyframes float {
    50% {padding-top: 20px}
    }

  @keyframes float {
    50% {padding-top: 20px}
    }
    




